import { Link } from 'react-router-dom';
import Lottie from 'lottie-web';
import { useEffect, useRef } from 'react';
import animeJson from '../../../animations/Illustration_404.json';
import './pageNotFound.css';

const PageNotFound = () => {
  const container = useRef(null);
  useEffect(() => {
    Lottie.loadAnimation({
      container: container.current,
      renderer: 'svg',
      loop: true,
      autoplay: true,
      animationData: animeJson,
    });
  }, []);
  return (
    <div className="page-not-found">
      <div className="page-not-found__img" ref={container} />
      <h1 className="page-not-found__title">404</h1>
      <p className="page-not-found__subtitle">–ö —Å–æ–∂–∞–ª–µ–Ω–∏—é, –∑–∞–ø—Ä–∞—à–∏–≤–∞–µ–º–∞—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞ –Ω–µ –Ω–∞–π–¥–µ–Ω–∞.</p>
      <p className="page-not-found__subtitle">–ü–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–µ—Ä–µ–π—Ç–∏ –Ω–∞ –≥–ª–∞–≤–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É</p>
      <Link to="/" className="button button_nodecoration button_color_blue">
        –í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞ –≥–ª–∞–≤–Ω—É—é
      </Link>
    </div>
  );
};
export default PageNotFound;
