import { useEffect, useState, useContext } from 'react';
import { useForm } from 'react-hook-form';
import Api from '../../../utils/api';
import CurrentUserContext from '../../../contexts/CurrentUserContext';
import QuestionCard from '../../Cards/QuestionCard/QuestionCard';
import QuestionsContainer from '../../Containers/QuestionsContainer/QuestionsContainer';
import Heading from '../../UI/Heading/Heading';
import ScrollContainer from '../../UI/ScrollContainer/ScrollContainer';
import {
  setActiveFilters,
  filterItemByFiltersList,
  getMultipleTagsIndex,
} from '../../../utils/filters';
import './QuestionsPage.css';

const QuestionsPage = () => {
  const currentUser = useContext(CurrentUserContext);
  const [activeTags, setActiveTags] = useState(['–í—Å–µ']);
  const [questions, setQuestions] = useState([]);
  const [tagList, setTagList] = useState([]);
  const [didAsk, setDidAsk] = useState(false);

  const {
    register,
    formState: { errors },
    handleSubmit,
    reset,
  } = useForm();

  const onSubmit = (questionData) => {
    console.log(questionData);
    setDidAsk(true);
    reset();
  };

  useEffect(() => {
    Api.getQuestions()
      .then((data) => {
        const qs = data.map((q) => ({
          tagNames: q.tags.map((tag) => tag.name),
          ...q,
        }));
        setQuestions(qs);

        const tagsData = qs
          .map((q) => q.tagNames)
          .flat()
          .filter((item, i, arr) => arr.indexOf(item) === i);

        setTagList(['–í—Å–µ', ...tagsData]);
      })
      .catch(console.log);
  }, []);

  const handleTagFilter = (tag) => {
    setActiveTags(setActiveFilters(activeTags, tag));
  };

  return (
    <>
      <section className="content main__section">
        <Heading>–û—Ç–≤–µ—Ç—ã –Ω–∞ –≤–æ–ø—Ä–æ—Å—ã</Heading>
        <div className="scroll-container">
          <ScrollContainer list={tagList} activeItems={activeTags} onClick={handleTagFilter} />
        </div>
        <QuestionsContainer place="questions">
          {questions
            .filter((q) => filterItemByFiltersList(activeTags, q.tagNames))
            .sort(
              (a, b) =>
                getMultipleTagsIndex(activeTags, a.tagNames) >
                getMultipleTagsIndex(activeTags, b.tagNames)
            )
            .map(({ title, answerText, tags, id }) => (
              <QuestionCard
                path={id}
                title={title}
                tags={tags}
                answerText={answerText}
                place="questions"
                key={id}
              />
            ))}
        </QuestionsContainer>
        {currentUser && (
          <>
            <form
              className={`questions__form ${didAsk ? 'questions__form_hidden' : ''}`}
              name="ask"
              action="/"
              onSubmit={handleSubmit(onSubmit)}
            >
              <h3 className="questions__form-heading">
                –ï—Å–ª–∏ –≤—ã –Ω–µ –Ω–∞—à–ª–∏ –æ—Ç–≤–µ—Ç –Ω–∞ —Å–≤–æ–π –≤–æ–ø—Ä–æ—Å ‚Äî –Ω–∞–ø–∏—à–∏—Ç–µ –Ω–∞–º, –∏ –º—ã –≤–∫–ª—é—á–∏–º –µ–≥–æ –≤&nbsp;—Å–ø–∏—Å–æ–∫
              </h3>
              <div className="questions__ask">
                <input
                  className={`questions__input ${
                    errors.questionText ? 'questions__input_error' : ''
                  }`}
                  type="text"
                  {...register('questionText', {
                    required: '–ó–∞–¥–∞–π—Ç–µ —Å–≤–æ–π –≤–æ–ø—Ä–æ—Å',
                    minLength: {
                      value: 2,
                      message: '–¢–µ–∫—Å—Ç –≤–æ–ø—Ä–æ—Å–∞ –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –Ω–µ –º–µ–Ω–µ–µ 2 —Å–∏–º–≤–æ–ª–æ–≤',
                    },
                  })}
                  id="askme"
                  placeholder={errors.questionText ? errors.questionText.message : '–í–≤–µ–¥–∏—Ç–µ –≤–æ–ø—Ä–æ—Å'}
                />
                <button type="submit" className="button">
                  –û—Ç–ø—Ä–∞–≤–∏—Ç—å
                </button>
              </div>
            </form>

            <p className={`questions__result ${!didAsk ? 'questions__form_hidden' : ''}`}>
              –°–ø–∞—Å–∏–±–æ! –ú—ã –ø—Ä–∏–Ω—è–ª–∏ –≤–∞—à –≤–æ–ø—Ä–æ—Å.
            </p>
          </>
        )}
      </section>
    </>
  );
};

export default QuestionsPage;
